function sum(a, b) {
  return a + b;
}

// named export
// ter vrios exports dentro do msm arquivo
// s pode chamar com msm nome
// precisa das chaves
export function sub(a, b) {
  return a - b;
}

function mult(a, b) {
  return a * b;
}

function div(a, b) {
  return a / b;
}

export { mult as multiplicacao, div };

// mtodo principal
// s pode ter um default por arquivo
// importar com qualquer nome
// no precisa de chaves
export default sum;



